import * as types from './mutation-types.js'
const matutations = {
  [types.SET_ISWHITE](state,isw){
    "use strict";
    state.isw=isw
  },
    [types.SET_TITLE](state,title){
    "use strict";
    state.title=title
  },
  [types.SET_SINGER_TYPE_INDEX](state,index){
    "use strict";
    state.index=index
  },
  [types.SELECTED_SINGER](state,singer){
    "use strict";
    state.singer=singer
  },
  [types.SET_ISPLAY](state,bol){
    "use strict";
    state.isPlay=bol
  },
  [types.SET_PLAYLIST](state,array){
    "use strict";
    state.playList=array
  },
  [types.SET_PLAYINDEX](state,num){
    "use strict";
    state.playIndex=num
  },
  [types.SET_PLAYPANEL](state,bol){
    "use strict";
    state.playPanel=bol
  },
}

export default matutations



