<template>
  <div class="content">
    <div class="banner">
      <img :src="discDetail.logo">
    </div>
    <div class="list">
      <ul>
        <li v-for="(item,index) in discDetail.songlist">
          <p class="pleft" @click="play(index,discDetail.songlist)">
            {{item.singer[0].name}} - {{item.songname}}
          </p>
          <div class="pright"><i></i></div>
        </li>
      </ul>
    </div>
    <loading v-if="!discDetail"></loading>
  </div>
</template>
<script>
  import {mapMutations} from 'vuex'
  import {mapGetters} from 'vuex'
  import {getDiscDetail} from '../api/getDiskDetail.js'
  import loading from '../components/loading.vue'
  export default{
    data(){
      return {
        discDetail:""
      }
    },
  components:{loading},
  created: function (){
    this.setTitle(window.localStorage.title);
    this._getDiscDetail(localStorage.dissid)
  },
    methods:{
      _getDiscDetail(id){
        getDiscDetail(id).then((res) =>{
          let left=res.search("{")
          let obj=eval(res.slice(left-1))
          console.log(obj.cdlist[0])
          this.discDetail=obj.cdlist[0]
        })
      },
      play(index,list){
        this.set_isPlay(true)
        this.set_playList(list)
        this.set_playIndex(index)
      },
      ...mapMutations({
        setTitle:"SET_TITLE",
        set_isPlay:"SET_ISPLAY",
        set_playList:"SET_PLAYLIST",
        set_playIndex:"SET_PLAYINDEX"
      })
    },
    destroyed(){
      window.localStorage.title=0
      document.body.scrollTop=0
      this.set_isPlay(false)
    }
  }
</script>
<style>
  .content{
    padding-top: 3.2143rem;
    width: 100%;
  }
  .banner{
    width: 100%;
    height: 12.5rem;
    position: relative;
    overflow: hidden;
  }
  .banner img{
    margin-top: -25px;
    width: 100%;
  }
  .list ul{
    padding-left: .7143rem;
    margin: 0;
  }
  .list li{
    width: 100%;
    height: 4.0714rem;
    border-bottom: 1px solid #e5e5e5;
    position: relative;
    display: table;
  }
  .pleft{
    width: 18.2rem;
    padding-right: .3571rem;
    display: table-cell;
    vertical-align: middle;
    padding-left: .3571rem;
    cursor: pointer;
    font-size: 1rem;
    box-sizing: border-box;
  }
  .list li:nth-child(1)>span{
    background: #e80000;
    color: #fff;
  }
  .list li:nth-child(2)>span{
    background: #ff7200;
    color: #fff;
  }
  .list li:nth-child(3)>span{
    background: #f8b300;
    color: #fff;
  }
  .pright{
    width: 2.5rem;
    height: 100%;
    cursor: pointer;
    text-align: center;
    vertical-align: middle;
    display: table-cell;
  }
  .pright i{
    display: inline-block;
    width: 1.0357rem;
    height: 1.0357rem;
    background: url("../assets/download_icon.png");
    background-size: 100%;
    margin: 0 auto;
  }
  .player{
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 4.2143rem;
    background: rgba(0,0,0,.9);
    color: #fff;
  }
  .player p{
    margin: 0;
    -webkit-margin-before: 0em;
    -webkit-margin-after: 0em;
  }
  .detail{
    width: 8rem;
    margin-top: -3.5rem;
    display: inline-block;
    vertical-align: middle;
  }
  .detail p:nth-child(1){
    width: 100%;
    line-height: 1.7rem;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    font-size: .8571rem;
    color: #fff;
  }
  .detail p:nth-child(2){
    line-height: 1.3;
    font-size: .7143rem;
    color: #888;
  }
  .player .img{
    height: 1.786rem;
    display: inline-block;
  }
  .player .img img{
    height: 1.786rem;
    width: 1.786rem;
    vertical-align: middle;
    margin-top: -3rem;
    margin-right: 0.5rem;
  }
</style>



