<template>
  <div class="rank">
    <ul>
      <li v-for="(item,index) in rankList" @click="dto(item)">
        <div class="rank-left">
          <img v-lazy="item.picUrl" alt=""/>
        </div>
        <div class="rank-center">
          {{item.topTitle}}
        </div>
        <div class="rank-right">
          <i></i>
        </div>
      </li>
    </ul>
    <loading v-if="!rankList"></loading>
  </div>
</template>
<script>
import originJsonp from 'jsonp'
import loading from '../components/loading.vue'
    export default{
        data(){
            return {
                rankList:""
            }
        },
        components:{loading},
        created(){
            var that=this
            originJsonp("https://c.y.qq.com/v8/fcg-bin/fcg_myqq_toplist.fcg?g_tk=67232076&uin=0&format=jsonp&inCharset=utf-8&outCharset=utf-8&notice=0&platform=h5&needNewCode=1&_=1506396138969",{
                param:'jsonpCallback'
            },function(err,data){
                that.rankList=data.data.topList
            })
        },
        destroyed(){
          document.body.scrollTop=0
        },
        methods:{
          dto(item){
                window.localStorage.title=item.topTitle
                window.localStorage.rankId=item.id
                this.$router.push('/rankDtail?id='+item.id)
          }
        }
    }
</script>
<style>
    .rank{
      padding-top: 6.4286rem;
      width: 100%;
    }
    .rank ul{
      padding: 0;
      margin: 0;
    }
  .rank li{
    list-style: none;
    display: block;
    position: relative;
    padding: .7143rem 2.14286rem .7143rem 7.1429rem;
    border-bottom: 1px solid #e5e5e5;
  }
  .rank-left{
    position: absolute;
    top: .7143rem;
    left: 0;
    width: 5.3751rem;
    height: 5.3751rem;
  }
  .rank-left img{
    width: 100%;
    margin-left: .7143rem;
  }
  .rank-center{
    min-height: 5.3751rem;
    line-height: 5.3751rem;
    display: block;
    font-size: .9rem;
    color: #333;
  }
  .rank-right{
    position: absolute;
    top: 0;
    right: 0;
    width: 2.14286rem;
    height: 100%;
    text-align: center;
    display: block;
  }
  .rank-right i{
    width: .57143rem;
    height: 1rem;
    margin-top: -0.5rem;
    position: absolute;
    top:50%;
    right: .7143rem;
    display: block;
    background: url("../assets/arrow_icon.png") no-repeat;
    background-size: 100%;
  }
</style>



