<template>
  <div>
    <div class="player" @click="showPlayer" v-show="detailPlay">
      <div style="width: 3.75rem;height: 3.75rem;padding: .2143rem .535rem .2143rem .2143rem;display: inline-table;">
        <img :src="imgUrl" alt="" width="100%" style="border-radius: 5px;"/>
      </div>
      <div class="detail">
        <p>{{body}}</p>
        <p>{{name}}</p>
      </div>
      <div class="img">
        <img :src="isRun" alt="" @click.stop="stopOrRun()"/>
        <img src="../assets/next_icon.png" alt="" @click.stop="next()"/>
        <img src="../assets/download_icon2.png" alt=""/>
      </div>
    </div>
    <audio :src="music" autoplay="autoplay" @timeupdate="updateTime"></audio>
    <div class="overlay" v-if="!detailPlay">
      <div class="bg-img" :style="'background-image: url('+imgUrl+')'"></div>
      <div class="bg-mask"></div>
      <div class="play">
        <div class="rankHead">
          <div class="back" @click="back()"><i></i></div>
          {{playing_music.songname}}
        </div>
        <div class="play-panel">
          <div class="img-box">
            <img :src="imgUrl" alt=""/>
          </div>
          <div class="lrc-box">
            <div :style="'margin-top:-'+lrcTop+'rem'">
              <p v-for="(item,index) in lrc.lines" :class="{handle:lineNum==index}">{{item.txt}}</p>
            </div>
          </div>
          <div class="time-wrap">
            <div>{{formate(palyedTime)}}</div>
            <progress-bar :playingPercent="playingPercent"></progress-bar>
            <div>{{formate(playing_music.interval)}}</div>
          </div>
          <div class="operate">
            <i class="prev" @click="prev"></i>
            <i :class="{isplay:true,stop:isRun!=='static/pause_icon.png'}" @click="stopOrRun"></i>
            <i class="next" @click="next"></i>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
  import {mapGetters} from 'vuex'
  import {mapMutations} from 'vuex'
  import {getLyric} from '../api/lrc.js'
  import {Base64} from 'js-base64'
  import progressBar from './progress'
  import lyric from 'lyric-parser'
  export default {
    name: 'player',
    data:function(){
      return {
        playing_music:"",
        imgUrl:"",
        body:"",
        name:"",
        music:"",
        musicId:0,
        isRun:"static/pause_icon.png",
        detailPlay:true,
        lrc:"[00:00]暂无歌词",
        palyedTime:"",
        playingPercent:0,
        lineNum:0,
        lrcTop:0
      }
    },
    components:{progressBar},
    computed:{
      ...mapGetters([
        'playList',
        'playIndex'
      ])
    },
    watch:{
      music:function(){
        setTimeout(() =>{
          document.querySelector('audio').play()
        },20)
        this.isRun="static/pause_icon.png"
      },
      playIndex:function(){
        this.set_data()
      },
      isRun:function(src){
        this.lrc.togglePlay()
        if(src=='static/pause_icon.png'){
            document.querySelector('audio').play()
        }else{
          document.querySelector('audio').pause()
        }
      }
    },
    created(){
        this.set_data()
        this.lrc=new lyric(this.lrc)
    },
    methods:{
      set_data(){
        this.playing_music=this.playList[this.playIndex].data?this.playList[this.playIndex].data:
                this.playList[this.playIndex].musicData?this.playList[this.playIndex].musicData:this.playList[this.playIndex]
        console.log(this.playing_music)
        this.imgUrl="https://y.gtimg.cn/music/photo_new/T002R300x300M000"+this.playing_music.albummid+".jpg?max_age=2592000"
        this.name=this.playing_music.singer[0].name
        this.body=this.playing_music.songname
        this.music='http://ws.stream.qqmusic.qq.com/'+this.playing_music.songid+'.m4a?fromtag=46'
        getLyric(this.playing_music.songmid).then((data) =>{
          if(data.code==0){
            if(this.lrc){this.lrc.stop()}
            this.lrc=new lyric(Base64.decode(data.lyric),this.handle)
            if(this.isRun=="static/pause_icon.png"){
              this.lrc.play()
            }
          }
        })
      },
      handle(msg){
        this.lineNum=msg.lineNum
        this.lrcTop=Math.max(0,(msg.lineNum-1)*1.7)
      },
      back(){
        this.detailPlay=true;
//        this.set_playPanel(false)
        document.body.style.overflow='scroll'
      },
      stopOrRun(){
        this.isRun=this.isRun=="static/pause_icon.png"?"static/play_icon.png":"static/pause_icon.png"
      },
      next(){
        if(this.playIndex==this.playList.length-1){
          this.set_playIndex(0)
        }else{
          this.set_playIndex(this.playIndex+1)
        }
        this.set_data()
        this.isRun="static/pause_icon.png"
      },
      showPlayer(){
        this.detailPlay=false;
//        this.set_playPanel(true)
        document.body.style.overflow='hidden'
      },
      prev(){
        if(this.playIndex==0){
          this.set_playIndex(this.playList.length-1)
        }else{
          this.set_playIndex(this.playIndex-1)
        }
        this.set_data()
      },
      updateTime(e){
        this.palyedTime= e.target.currentTime
        this.playingPercent=this.palyedTime/this.playing_music.interval
        if(this.playingPercent>=1){
          this.next()
        }
      },
      formate(time){
        time=time|0
        const minite=time/60|0
        var sec=time%60
        sec=sec.toString()
        sec=sec.length==1?("0"+sec):sec
        return (minite+":"+sec)
      },
      setTime(n){
        document.querySelector('audio').currentTime=n
        this.isRun="static/pause_icon.png"
        this.lrc.seek(n*1000)
      },
      ...mapMutations({
        set_isPlay:"SET_ISPLAY",
        set_playIndex:"SET_PLAYINDEX",
        set_playPanel:"SET_PLAYPANEL"
      })
    }
  }
</script>

<style>
  .player{
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 4.2143rem;
    background: rgba(0,0,0,.9);
    color: #fff;
  }
  .player p{
    margin: 0;
    -webkit-margin-before: 0em;
    -webkit-margin-after: 0em;
  }
  .detail{
    width: 8rem;
    margin-top: -3.5rem;
    display: inline-block;
    vertical-align: middle;
  }
  .detail p:nth-child(1){
    width: 100%;
    line-height: 1.7rem;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    font-size: .8571rem;
    color: #fff;
  }
  .detail p:nth-child(2){
    line-height: 1.3;
    font-size: .7143rem;
    color: #888;
  }
  .player .img{
    height: 1.786rem;
    display: inline-block;
  }
  .player .img img{
    height: 1.786rem;
    width: 1.786rem;
    vertical-align: middle;
    margin-top: -3rem;
    margin-right: 0.5rem;
  }
  .overlay{
    width: 100%;
    position: fixed;
    top: 3.2143rem;
    bottom: 0;
    z-index: 3;
    background: #fff;
  }
  .bg-img{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background:no-repeat 50%;
    background-size: cover;
    filter: blur(15px);
    -webkit-filter:blur(15px);
  }
  .bg-mask{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0,0,0,.6);
  }
  .play{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }
  .rankHead{
    width: 100%;
    line-height: 3rem;
    background: -webkit-linear-gradient(top,rgba(0,0,0,.6),transparent);
    padding: 0 2.1429rem;
    position: absolute;
    text-align: center;
    color: #fff;
    box-sizing: border-box;
    z-index: 1;
  }
  .back{
    width: 2.1429rem;
    height: 2.8031rem;
    position: absolute;
    top: 0;
    left: 0;
    text-align: center;
    cursor: pointer;
  }
  .play .back i{
    width: 0.85714rem;
    height: 1.42857rem;
    margin-top: .687265rem;
    display: inline-block;
    background: url("http://m.kugou.com/v3/static/images/index/goback_1.png") no-repeat;
    background-size: 100%;
  }
  .play-panel{
    width: 100%;
    padding-top: 3rem;
    position: relative;
  }
  .img-box{
    width: 11rem;
    height: 11rem;
    margin: 2rem auto 0;
  }
  .img-box img{
    width: 100%;
  }
  .lrc-box{
    height: 5.4rem;
    overflow: hidden;
    margin-top: 1rem;
    text-align: center;
    color: #afabac;
  }
  .lrc-box p{
    margin: 0;
    width: 100%;
    height: 1.7rem;
    line-height: 1.7rem;
    overflow: hidden;
    font-size: 0.85rem;
    vertical-align: top;
    white-space: nowrap;
  }
  .handle{
    color: #d1c90e;
  }
  .time-wrap{
    width: 100%;
    padding: .9rem .7rem;
    display: box;
    display: -webkit-box;
    box-align:center;
    -webkit-box-align:center;
    font-size: .6rem;
    color: #dcdcdc;
  }
  .operate{
    font-size: 0;
    text-align: center;
  }
  .operate .prev{
    background: url("../assets/play_prev.png") no-repeat;
    background-size: 100%;
    width: 2.4rem;
    height: 2.4rem;
    margin: .9rem;
    display: inline-block;
    vertical-align: middle;
  }
  .operate .isplay{
    background: url("../assets/play_pause.png") no-repeat;
    background-size: 100%;
    width: 3.2rem;
    height: 3.2rem;
    margin: .9rem;
    display: inline-block;
    vertical-align: middle;
  }
  .stop{
    background-image: url("../assets/play_play.png") !important;
  }
  .operate .next{
    background: url("../assets/play_next.png") no-repeat;
    background-size: 100%;
    width: 2.4rem;
    height: 2.4rem;
    margin: .9rem;
    display: inline-block;
    vertical-align: middle;
  }
</style>



