<template>
  <div class="mycontent">
    <div class="swiper-container">
      <div class="swiper-wrapper" @touchend="roll" @click="go">
        <div class="swiper-slide" v-for="item in bannerList">
          <img :src="item">
        </div>
      </div>
      <div class="swiper-pagination swiper-pagination-bullets">
        <span class="swiper-pagination-bullet" v-for="item in bannerList"></span>
      </div>
    </div>
    <div class="list">
      <ul>
        <li v-for="(item,index) in songList">
          <p class="left" @click="play(index,songList)">
            {{item.data.singer[0].name}} - {{item.data.songname}}
            <span v-if="item.data.albumdesc">【{{item.data.albumdesc}}】</span>
          </p>
          <div class="right"><i></i></div>
        </li>
      </ul>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
  import Swiper from '../../static/swiper.min.js'
  import '../../static/swiper.css'
  import banner from '../jsons/banner'
  import originJSONP from 'jsonp'
  import {mapMutations} from 'vuex'
  export default{
    data(){
      return {
//        songList: list_index,
        songList: [],
        bannerList:banner
      }
    },
    created(){
        let that=this
        window.sessionStorage.iscur=0
        originJSONP("https://c.y.qq.com/v8/fcg-bin/fcg_v8_toplist_cp.fcg?tpl=3&page=detail&date=2017-09-06&topid=27&type=top&song_begin=0&song_num=30&g_tk=5381&loginUin=0&hostUin=0&format=jsonp&inCharset=utf8&outCharset=utf-8&notice=0&platform=yqq&needNewCode=0",{
          param:'jsonpCallback'
        }, function (err,data) {
//          console.log(data)
          setTimeout(function () {
            that.songList=data.songlist
          },20)
        })
    },
    mounted () {
        this.banner = new Swiper('.swiper-container', {
          watchSlidesProgress : true,
          watchSlidesVisibility : true,
          pagination: '.swiper-pagination',
          paginationClickable: true,
          loop:true,
          autoplay: 2000,
          autoplayDisableOnInteraction : false
        })
      },
    components: {},
    methods:{
      go(){
        this.banner.stopAutoplay()
      },
      roll(){
        this.banner.startAutoplay()
      },
      play(index,songList){
        this.set_isPlay(true)
        this.set_playList(songList)
        this.set_playIndex(index)
      },
      ...mapMutations({
        set_isPlay:"SET_ISPLAY",
        set_playList:"SET_PLAYLIST",
        set_playIndex:"SET_PLAYINDEX"
      })
    },
    destroyed(){
      document.body.scrollTop=0
      this.set_isPlay(false)
    }
  }
</script>
<style>
  .mycontent{
    padding-top: 6.4286rem;
    width: 100%;
  }
  .swiper-container{
    margin-top: .3571rem;
  }
  .swiper-slide img{
    max-width: 100%;
    height: 8.5714rem;
    margin: 0 auto;
    display: block;
  }
  .swiper-pagination-bullet{
    width: .5714rem;
    height: .5714rem;
  }
  .swiper-pagination-bullet-active{
    background: #fff;
  }
  .mycontent .list ul{
    padding-left: .7143rem;
    margin: 0;
  }
  .mycontent .list li{
    width: 100%;
    height: 4.0714rem;
    border-bottom: 1px solid #e5e5e5;
    position: relative;
    display: table;
  }
  .mycontent .left{
    width: 18.2rem;
    padding-right: .3571rem;
    display: table-cell;
    vertical-align: middle;
    padding-left: .3571rem;
    cursor: pointer;
    font-size: 1rem;
    box-sizing: border-box;
  }
  .mycontent .right{
    width: 2.5rem;
    height: 100%;
    cursor: pointer;
    text-align: center;
    vertical-align: middle;
    display: table-cell;
  }
  .mycontent .right i{
    display: inline-block;
    width: 1.0357rem;
    height: 1.0357rem;
    background: url("../assets/download_icon.png");
    background-size: 100%;
    margin: 0 auto;
  }
</style>



