<template>
    <div class="progress" @click="go">
        <div class="progressed" :style="'width:'+playedProgress">
            <div class="progressing"
                 @touchmove.prevent="move"
                 @touchstart.prevent="start"
                 @touchend.prevent="end"
                 >
            </div>
        </div>
    </div>
</template>
<script type="text/ecmascript-6">
  export default {
    props:['playingPercent'],
    name: 'loading',
    data:function(){
        return {
            playedProgress:"",
            touch:{}
        }
    },
    watch:{
        playingPercent: function () {
            if(!this.touch.start){
                this.playedProgress=15*this.playingPercent+'rem'
            }
        }
    },
    methods:{
        start(e){
            this.touch.start=true
            this.touch.startX= e.touches[0].pageX
            this.touch.left=document.querySelector(".progressed").clientWidth
        },
        move(e){
            if(!this.touch.start){
                return
            }
            const detailX=e.touches[0].pageX-this.touch.startX
            const offsetWidth=Math.min(Math.max(0,this.touch.left+detailX),document.querySelector(".progress").clientWidth)
            this.playedProgress=offsetWidth+'px'
        },
        end(){
            this.touch.start=false
            let playpercent=document.querySelector(".progressed").clientWidth/document.querySelector(".progress").clientWidth
            this.$parent.setTime(this.$parent.playing_music.interval*playpercent)
        },
        go(e){
            const offsetWidth=document.querySelector('.progress').offsetLeft
            this.playedProgress=e.pageX-offsetWidth+'px'
          let playpercent=(e.pageX-offsetWidth)/document.querySelector(".progress").clientWidth
          this.$parent.setTime(this.$parent.playing_music.interval*playpercent)
        }
    }
  }
</script>

<style>
    .progress{
        height: .2rem;
        margin: 0 .8rem;
        background: #6c6b70;
        width: 15rem;
    }
    .progressing{
        position: absolute;
        width: .7rem;
        height: .7rem;
        background: #3195fd;
        border-radius: 50%;
        top: -0.25rem;
        right: -0.35rem;
    }
    .progressed{
        position: relative;
        background: #3195fd;
        height: 100%;
        width: 0;
    }
</style>



